<template>
  <div id="app">
    <transition name="fade" mode="out-in">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      loggedIn: false,
      content: "ON"
    };
  },
  methods: {
    test() {
      this.content = this.content === "ON" ? "OFF" : "ON";
    }
  }
};
</script>

<style lang="scss">
$bg-color: #3f3f3f;
$gc-color: #607d8b;
$main-color: rgb(252, 216, 15);

* {
  box-sizing: border-box;
}

body,
html {
  padding: 0;
  margin: 0;
  height: 100%;
  background: $bg-color;
}
.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  transition: opacity 0.2s ease;
}

.fade-leave-active {
  transition: opacity 0.2s ease;
  opacity: 0;
}

#app {
  font-family: "Lato", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;

  color: $main-color;
  font-size: 2rem;
}

.link {
  text-decoration: none;
  color: crimson;
}
</style>



