<template>
  <div id="game">
    <div id="game-container" v-if="dataLoaded">
      <div id="info-bubble" :style="itemInfo.css" ref="bubble" v-if="itemInfo.show">
        <div id="item-name">{{itemInfo.name}}</div>
        <div id="item-desc">{{itemInfo.desc}}</div>
      </div>
      <div id="crafting">
        <div id="crafting-ingredients">
          <div class="ingr i1"></div>
          <div class="ingr i2"></div>
        </div>
        <div id="crafting-result"></div>
      </div>
      <div id="inv">
        <div
          class="item"
          v-for="(item, index) in inv"
          :key="index"
          @click="getItem(item)"
          @mouseover="showItemInfo(item)"
          @mousemove="updateBubble($event)"
          @mouseleave="hideItemInfo()"
        >
          <img :src="getURL(item.type)" alt>
        </div>
      </div>
    </div>

    <div class="info" v-if="dataLoaded">
      <i>Your hero is signed in!</i>
      <span>{{nick}} | {{level}} lvl</span>
      <button class="logoutBtn" @click="logoutUser">Sign out</button>
    </div>

    <div class="disclaimer" v-if="dataLoaded">
      <span style="font-size: 3rem">Welcome, the glorious hero!</span>
      <span style="font-size: 2rem">
        This is an early version of
        <span style="color: red">Click n' Craft!</span>
        There's no much content here for now but in the future mechanics and design will be added!
      </span>
      <span style="font-size: 2.5rem">Hope for the best and so long!</span>
    </div>
  </div>
</template>

<script>
import firebase from "@/components/firebaseInit.js";

export default {
  name: "game",
  data() {
    return {
      dataLoaded: false,
      nick: null,
      level: 0,
      itemInfo: {
        show: false,
        name: null,
        desc: null,

        css: {
          top: 0,
          left: 0
        }
      },
      inv: [
        {
          type: "sword",
          name: "Sword of Hell",
          desc: ["Sword melt in The Hell."]
        },
        {
          type: "orb",
          name: "Glowing orb",
          desc: ["It glows in various hues."]
        }
      ],
      email: ""
    };
  },
  methods: {
    getItem(item) {},
    getURL(type) {
      return require("../assets/" + type + ".png");
    },
    showItemInfo(item) {
      this.itemInfo.show = true;

      this.itemInfo.name = item.name;
      this.itemInfo.desc = item.desc[0];

      console.log("Show info!");
    },
    hideItemInfo() {
      this.itemInfo.show = false;
      console.log("Hide info!");
    },
    updateBubble(e) {
      this.itemInfo.css.left = e.clientX + 15 + "px";
      this.itemInfo.css.top = e.clientY + 15 + "px";
    },
    getData() {
      const usersRef = firebase.db.collection("users");
      firebase.auth.onAuthStateChanged(user => {
        if (!user) return;

        usersRef
          .doc(user.uid)
          .get()
          .then(doc => {
            if (doc.exists) {
              const data = doc.data();

              this.nick = data.nick;
              this.level = data.level;

              this.dataLoaded = true;
            }
          })
          /* ERROR HANDLING! */
          .catch(err => alert(err));
      });
    },
    logoutUser() {
      firebase.auth.signOut();
    }
  },
  created() {
    this.getData();
  }
};
</script>

<style lang="scss" scoped>
$bg-color: #3f3f3f;
$gc-color: #607d8b;

$main-color: rgb(252, 216, 15);

.disclaimer {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  display: none;
  flex-direction: column;

  background: yellow;
  max-width: 700px;
  min-width: 400px;

  border: 4px solid black;
  border-radius: 2rem;
  padding: 1rem;

  font-size: 1rem;
  color: black;

  & > span:nth-child(1) {
    border-bottom: 2px solid black;
    padding: 5px;
  }

  & > span {
    padding: 0.5rem;
  }
}

#game {
  color: crimson;
  font-size: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: $bg-color;

  height: 100vh;
  overflow: hidden;
}

#game-container {
  background: $gc-color;
  width: 1100px;
  height: 700px;

  border-radius: 2rem;

  display: flex;
  flex-direction: column;
}

#crafting {
  background: #b83c17;

  height: 30%;

  border-radius: 2rem 2rem 3rem 3rem;

  display: flex;
  align-items: center;
  justify-content: space-around;

  & > #crafting-ingredients {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 50%;

    & > .ingr {
      border: 2px solid gold;

      width: 10rem;
      height: 10rem;

      background: url("../assets/sword.png");
      background-size: cover;
    }
  }

  & > #crafting-result {
    border: 2px solid black;

    width: 10rem;
    height: 10rem;
  }

  // & > #crafting-result, & .ingr {
  //     width: 30%;
  //     height: 100px;
  //     border: 2px solid black;
  // }
}

#inv {
  background: $gc-color;
  height: 70%;
  border-radius: 0 0 2rem 2rem;
  overflow: auto;

  display: flex;
  // justify-content: center;
  flex-flow: row wrap;
  align-content: flex-start;
  padding: 1rem;

  // background: rgba($color: #d8d8d8, $alpha: 0.5);

  & > .item {
    width: 7rem;
    height: 7rem;
    border: 2px solid cyan;
    margin: 1rem;
    transition: transform 1s, opacity 1s;

    & img {
      width: 100%;
      pointer-events: none;
      user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      -webkit-user-select: none;
    }

    background-size: cover;
  }
}

#info-bubble {
  position: absolute;
  top: 0;
  left: 10px;

  border-radius: 1rem;
  padding: 0.5rem;
  background: rgba($color: #e0dbdb, $alpha: 0.8);
  // visibility: hidden;
  z-index: 2;
  text-align: center;

  & > #item-name {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 5px;
    padding: 3px;
    border-bottom: 1px solid #3f3f3f;
  }

  & > #item-desc {
    font-size: 1rem;
    font-weight: 300;
    font-style: oblique;
    // text-align: left;
  }
}

.focused {
  position: relative;
  transform: translateY(-20px);
}

.focused::after {
  content: "";
  position: absolute;
  top: calc(100% + 13px);
  width: 100%;
  height: 0;
  background: #3f3f3f;
  box-shadow: #3f3f3f 0 5px 17px 6px;
  animation: opacityFocused 1s;
}

@keyframes opacityFocused {
  from {
    opacity: 0;
    top: 100%;
  }
  to {
    opacity: 1;
  }
}

.info {
  display: flex;
  flex-direction: column;

  position: fixed;
  top: 10px;
  right: 10px;

  background: $main-color;
  color: black;
  font-size: 1.3rem;
  padding: 10px;
  border-radius: 1rem;

  & > span {
    border-top: 1px solid black;
    padding: 5px;
    margin-top: 10px;
    font-weight: 600;
  }

  & > .logoutBtn {
    text-decoration: none;
    border: none;
    background: gray;
    color: white;
    margin: 15px auto 0 auto;
    padding: 8px;
    border-radius: 10px;
    font-size: 1rem;

    cursor: pointer;
  }
}
</style>



