<template>
  <div id="login">
    <h1>Continue your adventure
      <div>Click n' Craft v0.1a</div>
    </h1>
    <form id="signIn">
      <input id="email" v-model="email" type="text" placeholder="Your email..." required>
      <input id="pass" v-model="password" type="password" placeholder="Your passoword..." required>
      <button id="confirmBtn" @click.prevent="loginAccount">Continue your adventure!</button>
      <router-link id="signUpLink" to="/register">Create your first hero here!</router-link>
    </form>
  </div>
</template>

<script>
import firebase from "@/components/firebaseInit.js";

export default {
  name: "login",
  data() {
    return {
      nick: undefined,
      password: undefined,
      email: undefined
    };
  },
  methods: {
    loginAccount(e) {
      e.preventDefault();
      if (!this.password || !this.email) return;

      firebase.auth
        .signInWithEmailAndPassword(this.email, this.password)
        .then(cred => {
          console.log("Logged in!");
          this.$router.push("/");
        })
        .catch(err => {
          switch (err.code) {
            case "auth/user-not-found":
              alert(
                `Oops!\nThere's no account accociated with this email. :/\nCreate a new account instead!`
              );
              break;

            default:
              alert(`Ups!\n${err}`);
              break;
          }
        });
    }
  }
};
</script>

<style lang="scss" scoped>
$main-color: rgb(211, 207, 0);

h1 {
  & div {
    font-size: 1.5rem;
    font-style: italic;
    color: crimson;
  }
}

#login {
  position: absolute;
  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);
}

#signIn {
  margin: 0 auto;
  padding: 10px;
  display: flex;
  flex-direction: column;
  max-width: 800px;
  text-align: center;

  & > input {
    width: 400px;
    height: 50px;
    font-size: 1.5rem;
    text-align: center;
    font-weight: 500;
    margin: 10px auto;
    border-radius: 1rem;
    border: 1px solid gray;
    transition: all 0.3s;

    &:focus {
      border: 1px solid #04279b;
      color: rgb(155, 4, 34);
    }
  }

  & > button {
    min-height: 45px;
    width: 50%;
    margin: 1rem auto;
    font-size: 1.7rem;
    border: none;
    border-radius: 1rem;
    color: white;
    background: crimson;
    cursor: pointer;
    transition: all 0.3s;

    &:hover {
      background: #f80032;
    }
  }
}

#signUpLink {
  text-decoration: none;
  color: rgb(36, 18, 18);
  font-size: 1.2rem;
  background: whitesmoke;
  padding: 0.5rem;
  width: 40%;
  margin: 0 auto;
  border-radius: 1rem;
}
</style>



