<template>
  <div id="register">
    <h1>Sign up your new hero!
      <div>Click n' Craft v0.1a</div>
    </h1>
    <form id="signUp">
      <input id="nick" v-model="nick" type="text" placeholder="Your nickname..." required>
      <input id="email" v-model="email" type="text" placeholder="Your email..." required>
      <input id="pass" v-model="password" type="password" placeholder="Your passoword..." required>
      <button id="confirmBtn" @click="registerAccount">Let the journey begin!</button>

      <router-link id="signInLink" to="/login">Already a hero? Sign in here!</router-link>
    </form>
  </div>
</template>

<script>
import firebase from "./firebaseInit.js";

export default {
  name: "register",
  data() {
    return {
      nick: undefined,
      password: undefined,
      email: undefined
    };
  },
  methods: {
    registerAccount(e) {
      e.preventDefault();
      if (!this.nick || !this.password || !this.email) return;

      this.createUser(this.email, this.password, this.nick);
    },
    createUser(email, pwd, nick) {
      firebase.auth
        .createUserWithEmailAndPassword(email, pwd)
        .then(cred => {
          firebase.db
            .collection("users")
            .doc(cred.user.uid)
            .set({
              level: 1,
              nick: nick,
              inventory: ["stone", "stick"]
            })
            .then(() => {
              console.log("Użytkownik został wpisany do bazy!");
              this.$router.push("/");
            });
        })
        .catch(err => alert(err));
    }
  }
};
</script>

<style lang="scss" scoped>
h1 {
  & div {
    font-size: 1.5rem;
    font-style: italic;
    color: crimson;
  }
}

#register {
  position: absolute;
  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);
}

#signUp {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  max-width: 800px;

  & > input {
    width: 400px;
    height: 50px;
    font-size: 1.5rem;
    text-align: center;
    font-weight: 500;
    margin: 10px auto;
    border-radius: 1rem;
    border: 1px solid gray;
    transition: all 0.3s;

    &:focus {
      border: 1px solid #04279b;
      color: rgb(155, 4, 34);
    }
  }

  & > button {
    min-height: 45px;
    width: 50%;
    margin: 1rem auto;
    font-size: 1.7rem;
    border: none;
    border-radius: 1rem;
    color: white;
    background: crimson;
    cursor: pointer;
    transition: all 0.3s;

    &:hover {
      background: #f80032;
    }
  }
}

#signInLink {
  text-decoration: none;
  color: rgb(36, 18, 18);
  font-size: 1.2rem;
  background: whitesmoke;
  padding: 0.5rem;
  width: 40%;
  margin: 0 auto;
  border-radius: 1rem;
}
</style>



